<template>
  <v-card>
    <v-card-title>
      <span class="headline">{{ title }}</span>
    </v-card-title>
    <v-card-text>
      <v-form v-model="valid" ref="form" lazy-validation>
        <v-row dense>
          <v-col cols="12">
            <v-text-field
              required
              v-model="formValues.name"
              label="Flow network name"
              :rules="[rules.required]"
            ></v-text-field>
          </v-col>
          <v-col cols="12" v-if="isMasterEnabled">
            <v-switch v-model="formValues.is_master_slave" label="Master's Slave"></v-switch>
          </v-col>
          <!-- FLOW IP begin -->
          <v-col cols="12" v-if="!formValues.is_master_slave">
            <v-text-field :rules="[rules.required]" v-model="formValues.flow_ip" label="Flow IP"></v-text-field>
          </v-col>
          <v-col cols="12" v-if="!formValues.is_master_slave">
            <v-text-field
              :rules="[rules.required, rules.numberRule]"
              type="number"
              v-model="formValues.flow_port"
              label="Flow Port"
            ></v-text-field>
          </v-col>
          <v-col cols="12" v-if="!formValues.is_master_slave">
            <v-switch v-model="formValues.flow_https" label="Enable HTTPS"></v-switch>
          </v-col>

          <v-col cols="12" v-if="!formValues.is_master_slave">
            <v-text-field
              required
              :rules="[rules.required]"
              v-model="formValues.flow_username"
              label="Flow Username"
            ></v-text-field>
          </v-col>
          <v-col cols="12" v-if="!formValues.is_master_slave">
            <v-text-field
              type="password"
              v-model="formValues.flow_password"
              :rules="[rules.required]"
              label="Flow Password"
              autocomplete="new-password"
            ></v-text-field>
          </v-col>
          <!-- FLOW IP end -->
          <!-- Fetch history start -->
          <v-col cols="12">
            <v-switch v-model="formValues.fetch_histories" label="Enable Histories"></v-switch>
          </v-col>
          <v-col v-if="formValues.fetch_histories" cols="12">
            <v-text-field
              type="number"
              v-model="formValues.fetch_hist_frequency"
              label="Fetch History Frequency"
            ></v-text-field>
          </v-col>
          <v-col v-if="formValues.fetch_histories" cols="12">
            <v-switch v-model="formValues.delete_histories_on_fetch" label="Delete Histories after fetch"></v-switch>
          </v-col>
          <!-- Fetch history end -->
        </v-row>
      </v-form>
    </v-card-text>
    <form-error :error="error"></form-error>
    <v-spacer></v-spacer>
    <v-card-actions class="align-end justify-end">
      <v-btn color="darken-1" text @click="cleanUp"> Cancel </v-btn>
      <v-btn color="blue darken-1" :loading="isSaving" text @click="onSubmit(formValues)"> {{ saveTitle }} </v-btn>
    </v-card-actions>
  </v-card>
</template>
<script>
import FormError from '../../Common/FormError.vue';
import TooltipIconButton from '../../RubixApps/TooltipIconButton.vue';

export const initialFormValues = () => ({
  name: null,
  fetch_histories: false,
  fetch_hist_frequency: 0,
  delete_histories_on_fetch: false,
  flow_ip: '0.0.0.0',
  flow_port: 1616,
  flow_https: false,
  flow_username: null,
  flow_password: null,
  flow_token: null,
  is_mqtt: false,
  is_master_slave: false,
});

export default {
  name: 'flow-network-form',
  components: { TooltipIconButton, FormError },
  props: ['isEdit', 'onCancel', 'onSave', 'item', 'isSaving', 'isMasterEnabled'],
  data() {
    return {
      rules: {
        required: value => !!value || 'Required.',
        numberRule: v => !isNaN(parseFloat(v)) || 'Required.',
      },
      error: {},
      valid: false,
      formValues: {
        ...initialFormValues(),
      },
    };
  },
  computed: {
    title() {
      return this.isEdit ? 'Edit Flow Network' : 'Create Flow Network';
    },
    saveTitle() {
      return this.isEdit ? 'Save' : 'Create';
    },
  },
  mounted() {
    if (this.isEdit && this.item) {
      this.formValues = {
        ...this.formValues,
        ...this.item,
      };
    }
  },
  methods: {
    cleanUp() {
      this.formValues = {};
      if (typeof this.onCancel === 'function') this.onCancel();
    },
    preparePayload(formValues) {
      if (formValues.is_master_slave && this.isMasterEnabled) {
        delete formValues.flow_ip;
        delete formValues.flow_port;
        delete formValues.flow_https;
        delete formValues.flow_token;
        delete formValues.flow_username;
        delete formValues.flow_password;
      }
      formValues.flow_port = Number(formValues.flow_port);
      return formValues;
    },
    onSubmit() {
      this.error = {};
      if (!this.$refs.form.validate()) {
        return;
      }
      if (typeof this.onSave !== 'function') {
        throw new Error('Flow network form onSave function not passed');
      }
      return this.onSave(this.preparePayload(this.formValues))
        .then(() => {
          this.$toasted.show(`Successfully ${this.isEdit ? 'updated' : 'created'} flow network.`);
          this.cleanUp(true);
        })
        .catch(err => {
          this.error = err;
          this.$toasted.show(`Failed to ${this.isEdit ? 'update' : 'create'} flow network.`, { type: 'error' });
        });
    },
  },
};
</script>
